.home{
    width: 100%;
}
.home_search{
    position: fixed;
    z-index: 10;
    height: 6%;
    width: 100%;
    background-color:#FF852A;
}
.home_inp{
    position: absolute;
    top: 13%;
    left: 13%;
    text-align: center;
    background-color:#FF4E22;
    color: white;
    border: 0;
    width: 80%;
    height: 70%;
    border-radius: 5px;
}
.home_content{
    width: 100%px;
    margin-bottom: 100px;
}
.home_xihuan{
    width: 100%;
    height: 20%;
    color: #FF4E22;
    font-size: 17px;
    font-family: 'Microsoft soft';
    text-align: center;
    padding: 6px 0px;
}
.home_photo{
    width: 100%;
    height: 1230px;
}
.home_photo_shop{
    float: left;
    margin-left: 3%;
    margin-top: 3%;
    background-color: white;
    width: 46%;
    border-radius: 5px;
    padding-bottom: 4px;
}
.home_photo_shop img{
    width: 100%;
    height: 65%;
    border-radius: 5px 5px 0 0;
}
.home_box{
    margin-left: 10px;
    font-family: Microsoft soft;
    font-size: 16px;
    line-height: 20px;
    height: 40px;
    overflow: hidden;
}
.how_much{
    margin-left: 10px;
    margin-top: 6px;
    font-size: 14px;
    color: #FF4E22;
}
.how_many{
    margin-left: 70px;
    margin-top: -15px;
    font-size: 14px;
    color:grey;
}























